<template>
  <div class="modal-mask">
    <div class="modal-wrapper" @click="closePanel">
      <div class="modal-container container auth-container" @click.stop @scroll.stop>
        <div class="auth-panel">
          <div class="auth-panel-nav">
            <router-link to="/">
              <img src="@/assets/img/landing/logo.png" alt="logo" />
            </router-link>
            <footer v-if="authType == 'login' || authType == 'reset'">
              <p>Nie masz konta?</p>
              <router-link
                class="register-button"
                to="/auth/register/patient"
              >Zarejestruj się</router-link>
            </footer>
            <footer v-if="authType == 'register'">
              <p>Posiadasz już konto?</p>
              <router-link class="login-button" to="/auth/login">Zaloguj się</router-link>
            </footer>
          </div>
          <transition name="form-animation" mode="out-in">
            <router-view></router-view>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      showAuthModal: false,
      authType: this.$route.name
    };
  },
  watch: {
    $route(to, from) {
      this.authType = this.$route.name;
    }
  },
  methods: {
    closePanel() {
      this.$router.push("/");
    }
  }
};
</script>