import Vue from 'vue'
import VueRouter from 'vue-router'
// Landing

import landingLayout from '../views/landing/index';
import authPanel from '../views/landing/auth/auth-panel';
import loginForm from '../views/landing/auth/login';
import resetForm from '../views/landing/auth/reset';
import registerForm from '../views/landing/auth/register';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: landingLayout,
    children: [
      {
        path: 'auth',
        component: authPanel,
        children: [
          {
            path: 'login',
            component: loginForm,
            name: 'login'
          },
          {
            path: 'reset',
            component: resetForm,
            name: 'reset'
          },
          {
            path: 'register/:regType',
            component: registerForm,
            name: 'register'
          }
        ]
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router

