<template>
  <div class="register-panel">
    <h1>Zarejestruj się <router-link to="/">x</router-link></h1>
    <div class="account-type-box">
      <router-link to="/auth/register/doctor" :class="{'acc-type-option': true, 'active': registerType == 'doctor'}">Lekarz</router-link>
      <router-link to="/auth/register/patient" :class="{'acc-type-option': true, 'active': registerType == 'patient'}">Pacjent</router-link>
      <router-link to="/auth/register/institution" :class="{'acc-type-option': true, 'active': registerType == 'institution'}">Placówka</router-link>
    </div>
    <form v-if="registerType == 'doctor'">
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/person.png" alt="">
        <input id="firstname" name="firstname" v-model="doctor.firstname" type="text" placeholder="Imie">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/person.png" alt="">
        <input id="lastname" name="lastname" v-model="doctor.lastname" type="text" placeholder="Nazwisko">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/skill.png" alt="">
        <input id="skill" name="skill" v-model="doctor.skill" type="text" placeholder="Specjalizacja">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
        <input id="street" name="street" v-model="doctor.street" type="text" placeholder="Ulica i numer budynku">
      </div>
      <div class="small-inputs">
        <div class="input-row">
          <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
          <input id="city" name="city" v-model="doctor.city" type="text" placeholder="Miasto">
        </div>
        <div class="input-row">
          <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
          <input id="zip" name="zip" v-model="doctor.zip" type="text" pattern="[0-9]{2}\-[0-9]{3}{5}" placeholder="Kod pocztowy">
        </div>
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/mail.png" alt="">
        <input id="email" name="email" v-model="doctor.email" type="email" placeholder="E-mail">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/phone.png" alt="">
        <input id="phone" name="phone" v-model="doctor.phone" type="tel" placeholder="Numer telefonu">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/password.png" alt="">
        <input id="password" name="password" v-model="doctor.password" type="password" placeholder="Hasło">
      </div>
      <md-checkbox class="terms-checkbox" v-model="doctor.terms">Oświadczam, że znam i akceptuje postanowienia regulaminu portalu Book Your Health.</md-checkbox>
      <div class="submit">
        <input type="submit" class="register-button" value="Zarejestruj się">
        <input type="reset" class="reset-button" value="Reset">
      </div>
    </form>
    <form v-if="registerType == 'patient'">
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/person.png" alt="">
        <input id="firstname" name="firstname" v-model="doctor.firstname" type="text" placeholder="Imie">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/person.png" alt="">
        <input id="lastname" name="lastname" v-model="doctor.lastname" type="text" placeholder="Nazwisko">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/person.png" alt="">
        <input id="pesel" name="pesel" v-model="doctor.skill" type="text" placeholder="PESEL">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
        <input id="street" name="street" v-model="doctor.street" type="text" placeholder="Ulica i numer budynku">
      </div>
      <div class="small-inputs">
        <div class="input-row">
          <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
          <input id="city" name="city" v-model="doctor.city" type="text" placeholder="Miasto">
        </div>
        <div class="input-row">
          <img class="input-icon" src="../../../assets/img/icons/layout/map.png" alt="">
          <input id="zip" name="zip" v-model="doctor.zip" type="text" pattern="[0-9]{2}\-[0-9]{3}{5}" placeholder="Kod pocztowy">
        </div>
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/mail.png" alt="">
        <input id="email" name="email" v-model="doctor.email" type="email" placeholder="E-mail">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/phone.png" alt="">
        <input id="phone" name="phone" v-model="doctor.phone" type="tel" placeholder="Numer telefonu">
      </div>
      <div class="input-row">
        <img class="input-icon" src="../../../assets/img/icons/layout/password.png" alt="">
        <input id="password" name="password" v-model="doctor.password" type="password" placeholder="Hasło">
      </div>
      <md-checkbox class="terms-checkbox" v-model="doctor.terms">Oświadczam, że znam i akceptuje postanowienia regulaminu portalu Book Your Health.</md-checkbox>
      <div class="submit">
        <input type="submit" class="register-button" value="Zarejestruj się">
        <input type="reset" class="reset-button" value="Reset">
      </div>
    </form>
    <form v-if="registerType == 'institution'"></form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      registerType: this.$route.params.regType,
      doctor: {
        firstname: '',
        lastname: '',
        skill: '',
        street: '',
        city: '',
        zip: '',
        email: '',
        phone: '',
        password: '',
        terms: false
      }
    };
  },
  watch: {
    $route(to, from) {
      this.registerType = this.$route.params.regType;
    }
  }
};
</script>
<style scoped>
::-webkit-scrollbar {
    -webkit-appearance:none;
    width: 7px;
}
::-webkit-scrollbar-track {
    background:#f1f1f1;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    background:#ddd;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
    background:#aaa;
}
</style>